*,
*::before,
*::after {
  box-sizing: border-box;
}
html,
body {
  margin: 0px;
  padding: 0px;
  bottom: 0;
  left: 0;
}

.navbar-block {
  display: block;
  position: relative;
  width: 225px;
  height: 900px;
  background-color: #f7f7f7;
  right: 14px;
}
.dropbtn {
  background-color: transparent;
  padding: 10px;
  cursor: pointer;
  border: none;
  width: 225px;
  font-weight: bold;
  position: relative;
  text-align: left;
  margin-left: 20px;
}
/* Cambie aqui */
.dropdown:hover .drop-menu {
  display: inline-block;
}
.dropbtn:focus {
  outline: none;
}
.drop-menu {
  display: none;
  position: relative;
  background-color: transparent;
  width: 225px;
  z-index: 2;
  transition: display 6s;
}
@keyframes example {
  from {
    height: 0;
  }
  to {
    height: 300px;
  }
}

.link-menu {
  color: black;
  padding: 8px 10px;
  text-decoration: none;
  display: block;
  position: relative;
  text-align: left;
  margin-left: 19px;
}

.link-menu:hover {
  background-color: whitesmoke;
  text-decoration: none;
  color: black;
  border-left: 5px solid #61dafb;
  height: auto;
  font-weight: bold;
}
@media (max-width: 400px) {
  .colocacion {
    display: flex;
    justify-content: center;
    margin: 10px 0;
    text-align: center;
  }
}
.navbar-act {
  display: flex;
  list-style-type: none;
  margin: 0;
  padding: 0;
  height: 150px;
}
.link-act-li {
  display: flex;
  justify-content: center;
  align-items: center;
  flex: 1 1 auto;
  text-decoration: none;
  transition: flex-grow 1s ease;
  position: relative;
}
.link-act-li:hover {
  flex-grow: 3;
}
.link-act-li:hover .link-act {
  transform: scale(2, 2);
}
.link-act {
  text-decoration: none;
  position: relative;
  color: black;
  transition: transform 1s ease;
}

.link-act:hover {
  text-decoration: none;
  font-weight: bold;
  color: black;
}
.img-act {
  width: 30px;
  height: 30px;
  position: relative;
}
